.layout {
  display: flex;
  flex-direction: row-reverse;
  background-color: #e6e6e6;
  height: 100vh;
  overflow: hidden;
}

.notice {
  background-color: #fff;
  padding: 1rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
  max-width: 80%;
  border-radius: 10px;
  margin: auto auto 2rem;
}

.notice p {
  margin: 0;
}

.print-button {
  padding: .7rem 2rem;
  margin-left: 4.5rem;
  margin-top: 2rem;
  display: inline-block;
  text-align: center;
  background-color: rgba(128, 0, 128, 0.9);
  border: 1px solid rgba(77, 0, 77, 0.9);
  border-radius: 6px;
  font-weight: 500;
  color: #fff;
}

.print-button svg {
  fill: currentColor;
}

@media (min-width: 1200px) {
  .print-button {
    margin-left: 7.5rem;
  }
}

@media print {
  .print-button {
    display: none !important;
  }
}

.print-button:focus {
  outline: none;
  box-shadow: 0 0 0 0.16rem rgba(128, 0, 128, 0.4);
  transition: box-shadow ease-in 0.3s;
}

.header header h1, .header header h3 {
  font-size: 1.62rem;
  margin: 0;
}

.header {
  padding: 3rem 4.5rem 1rem;
  background-color: #fff;
  position: relative;
}

@media (min-width: 1200px) {
  .header {
    padding-left: 7.5rem;
  }
}

.header header h1 {
  color: #b3b3b3;
}

.header header h3 {
  color: #333333;
}

.header header .avatar {
  position: absolute;
  right: 4.5rem;
  top: 2.5rem;
  border-radius: 50%;
  padding: .3rem;
  background-color: #fff;
}

.header header .avatar > .avatar-img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}

.sidebar, .sidebar .favicon {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.sidebar {
  width: 270px;
  flex-shrink: 0;
  background-color: purple;
  height: 100vh;
  padding: 2rem;
}

.sidebar .favicon .favicon-img {
  width: 170px;
}

.sidebar .favicon .favicon-img:last-of-type {
  display: none;
}

@media print {
  .sidebar .favicon .favicon-img:first-of-type {
    display: none;
  }
  .sidebar .favicon .favicon-img:last-of-type {
    display: block;
  }
}

.sidebar .footer {
  margin-bottom: 2rem;
  margin-top: auto;
}

.sidebar .footer .group {
  color: #fff;
}

.sidebar .footer .group p {
  color: #f3bbf3;
  margin-bottom: 0;
  font-weight: 600;
}

.sidebar .footer .group a {
  word-break: break-all;
  color: #f3bbf3;
  text-decoration: underline dotted;
}

@media print {
  .sidebar * {
    color: #000 !important;
  }
}

.candidate-info {
  margin: 7rem auto;
  padding: 0 4.5rem;
  max-width: 100%;
}

@media (min-width: 1200px) {
  .candidate-info {
    padding: 0 7.5rem;
  }
}

.candidate-info table tr td:first-of-type {
  width: 50% !important;
}

.candidate-info .result table {
  width: 100%;
}

.candidate-info .result table tbody tr td {
  font-size: 1.5em;
  font-weight: 600;
  padding: 0.2rem 1rem;
}

.candidate-info .result table tbody tr td:first-of-type span {
  color: #444444;
}

.candidate-info .result table tbody tr td:last-of-type {
  color: #187718;
  color: #340034;
}

.candidate-info .data {
  box-shadow: 0 0.2rem 0.419rem rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.candidate-info .data:not(:last-of-type) {
  margin-bottom: 2.5rem;
}

.candidate-info .data .table-head {
  padding: 0.2rem 1rem;
  border-radius: 10px 10px 0 0;
  background-color: purple;
  color: #fff;
  width: 100%;
  font-weight: 600;
}

.candidate-info .data table {
  background-color: #fff;
  border-radius: 0 0 10px 10px;
  position: relative;
  width: 100%;
  border-collapse: collapse;
}

.candidate-info .data table tbody tr {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.candidate-info .data table tbody tr td {
  color: #444444;
  padding: 0.2rem 1rem;
}

